
.home__container {
    row-gap: 7rem;
}

.home__content {
    grid-template-columns: 116px repeat(2,1fr);
    padding-top: 2.5rem;
    column-gap: 2rem;
    align-items: center;
}

.home__social {
    display: grid;
    grid-template-columns: max-content;
    row-gap: 1rem;
}
.home__social-icon{
    color: rgb(40, 167, 222);
    font-size: 1.2rem;
}

.home__title {
    font-size: 2rem;
    margin-bottom: 0.25rem ;
}
.home__subtitle{
    position: relative;
    font-size: 1.1rem;
    padding-right: 5.4rem;
    font-weight: 400;
    margin-bottom: 1rem;
}
.home__description {
    max-width: 400px;
    margin-bottom: 3rem;
    /* color: black; */
}

.home__hand {
    width:  30px;
    height: 30px;
    margin-left: 0.3rem;
}
.home__img {
    background: url(../../assets/Saurav.png);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    box-shadow: inset 0 0 0 9px rgba(8, 7, 7, 0.461) ;
    order: 1;
    justify-self: center;
    width: 250px;
    height: 250px;
    border-radius: 50%;
    /* animation: profile__animate 8s ease-in-out infinite 1s; */
}

@keyframes profile__animate {

    0% {
        border-radius: 60% 40% 30% 70%/60% 30% 70% 40% ;
    }

    50% {
        border-radius: 30% 60% 70% 40%/50% 60% 30% 60% ;
    }

    100% {
        border-radius: 60% 40% 30% 70%/60% 30% 70% 40% ;
    }
}

@keyframes scroll {
    0% {
        transform: translateY(0);
    }
    30% {
        transform: translateY(3.75rem);
    }
}

.home__scroll {
    margin-left: 9.25rem;
}
.home__scroll-name{
    color: rgb(203, 195, 195);
    font-weight: 500;
    margin-right: 0.25rem;
    margin-left: 2px;
}
.home__scroll-arrow {
      font-size: 1.25rem;
      color: white;
}
/* ================BREAKPOINS===================== */
@media screen and (max-width : 992px) {

    .home__content {
        grid-template-columns: 100px repeat(2,1fr);
        column-gap: 1.25rem;
        /* border: 1px solid red; */
    }
    .home__hand {
        width: 26px;
        height: 26px;
    }
    .home__subtitle {
        padding-left: 3.75rem;
        margin-bottom: 1rem;
    }
    .home__subtitle ::before {

        width: 42px;
        top: 0.8rem;
    }

    .home__description {
        max-width: initial;
        margin-bottom: 2.5rem;
    }
    .home__img {
        width: 250px;
        height: 250px;
        box-shadow: inset 0 0 0 8px rgba(8, 7, 7, 0.461) ;
    }

    .home__scroll {
        margin-left: 7.5rem;
    }
}

/* ====================MEDIUM SCREEN================= */

@media screen and (max-width : 768px){

    .home__content {
        grid-template-columns: 0.5fr 3fr;
        padding-top: 3.5rem;
        /* border: 1px solid red; */
        text-align: center;
        margin-left: 15%;
    }
    .home__img{
        order: initial;
        justify-self: initial;
        
    }
    .home__subtitle {
        padding-left: 4.5rem;
        margin-bottom: 1rem;
    }

    .home__data {
        grid-column: 1/3;
    }
    .home__img {
        box-shadow: inset 0 0 0 6px rgba(8, 7, 7, 0.461) ;
        width: 200px;
        height: 200px;
        display: block;
        margin-left: 20%;
        /* border: 1px solid red; */
    }
    .home__title {
        align-items: center;
        /* margin-left: 42%; */
    }
    .home__scroll {
        display: none;
    }
}
@media screen and (max-width : 606px) {
    .home__container {
        /* border: 1px solid red; */
        width: 95%;
        margin-left: 20%;
    }
}

@media screen and (max-width : 585px){
    .home__container{
        /* border: 1px solid red; */
        width: 90%;
        margin-left: 24%;
    }
}

@media screen and (max-width : 575px){
    .home__container{
        /* border: 1px solid red; */
        width: 86%;
        margin-left: 3%;
    }
    .home__img {
        display: block;
       margin-left: 10%;
    }

}
@media screen and (max-width : 350px){

    .home__container{
        /* border: 1px solid red; */
        width: 95%;
        /* margin-left: 3%; */
    }
    .home__content{
        /* border: 1px solid blue; */
        margin-left: 0px;

    }

    .home__img {
        display: block;
        width: 200px;
        height: 200px;
        margin-left: 7%;
        /* border: 1px solid red; */
    }
    .home__hand {
        width: 27px;
        height: 27px;
       margin-left: 10%;
    }
    .home__title{
        margin-left: 60px;
        font-size: 27px;
        /* border: 1px solid red; */
    }
    .home__subtitle{
        margin-left: 55px;
        font-size: 16px;
        /* border: 1px solid red; */
    }
    .home__description {
        max-width: initial;
        margin-bottom: 2.5rem;
        margin-left: 40px;
        font-size: 18px;
        /* border: 1px solid red; */
    }
    .button {
       display: block;
       margin-left: 290px;
        padding: 2rem 4rem;
        width: 100px;
    }
}